@import url(https://fonts.googleapis.com/css?family=Karla|Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background: -webkit-linear-gradient(135deg, #006a9a, #37093e);
  background: linear-gradient(315deg, #006a9a, #37093e);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(40px + 2vmin);
  color: white;
  font-family: Karla;
}

.Sub-header {
  padding-top: 12px;
  padding-bottom: 20px;
  font-size: calc(10px + 2vmin);
  color: #ededed;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.locationContainer {
  padding-top: 25px;
  padding-bottom: 25px;
}

.resultContainer {
  height: 100vh;
}

.sliderContainer {
  padding-bottom: 50px;
}

.content {
  padding-top: 100px;
  font-family: Roboto;
}

.intro {
  padding-bottom: 100px;
  font-size: 40px;
}

h3 {
  font-weight: 100;
}

h1 {
  font-weight: bold;
}

.entryContainer {
  height: 70vh;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  width: 100%;
}

.locationList {
  justify-content: center;
  align-items: center;
  max-height: 350px;
  overflow: auto;
}

.result {
  justify-content: center;
  align-items: center;
}

.listItem:hover {
  background: #cccccc;
}

.add {
  padding-top: 10px;
  justify-content: center;
  align-items: center;
}

.slider {
  padding-bottom: 100px;
}

.resultBox {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.list {
  justify-content: center;
  align-items: center;
  max-height: 100vh;
  overflow: auto;
  width: 25vw;
}

.loadingbar {
  padding-top: 45vh;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
}

